namespace $ {
	export class $hyoo_tree extends $mol_book2 {

		/**
		 * ```tree
		 * plugins / <= Theme
		 * ```
		 */
		plugins() {
			return [
				this.Theme()
			] as readonly any[]
		}

		/**
		 * ```tree
		 * pages /
		 * 	<= Source
		 * 	<= Result
		 * ```
		 */
		pages() {
			return [
				this.Source(),
				this.Result()
			] as readonly any[]
		}

		/**
		 * ```tree
		 * Theme $mol_theme_auto
		 * ```
		 */
		@ $mol_mem
		Theme() {
			const obj = new this.$.$mol_theme_auto()

			return obj
		}

		/**
		 * ```tree
		 * Lights $mol_lights_toggle
		 * ```
		 */
		@ $mol_mem
		Lights() {
			const obj = new this.$.$mol_lights_toggle()

			return obj
		}

		/**
		 * ```tree
		 * compile_label @ \Compile
		 * ```
		 */
		compile_label() {
			return this.$.$mol_locale.text( '$hyoo_tree_compile_label' )
		}

		/**
		 * ```tree
		 * Compile $mol_link
		 * 	sub / <= compile_label
		 * 	arg * compile \
		 * ```
		 */
		@ $mol_mem
		Compile() {
			const obj = new this.$.$mol_link()

			obj.sub = () => [
				this.compile_label()
			] as readonly any[]
			obj.arg = () => ({
				compile: ""
			})

			return obj
		}

		/**
		 * ```tree
		 * source?val \
		 * ```
		 */
		@ $mol_mem
		source(val?: any) {
			if ( val !== undefined ) return val
			return ""
		}

		/**
		 * ```tree
		 * source_hint @ \view.tree source
		 * ```
		 */
		source_hint() {
			return this.$.$mol_locale.text( '$hyoo_tree_source_hint' )
		}

		/**
		 * ```tree
		 * Source_text $mol_textarea
		 * 	value?val <=> source?val
		 * 	hint <= source_hint
		 * ```
		 */
		@ $mol_mem
		Source_text() {
			const obj = new this.$.$mol_textarea()

			obj.value = (val?: any) => this.source(val)
			obj.hint = () => this.source_hint()

			return obj
		}

		/**
		 * ```tree
		 * Source $mol_page
		 * 	title @ \View.Tree
		 * 	tools /
		 * 		<= Lights
		 * 		<= Compile
		 * 	body / <= Source_text
		 * ```
		 */
		@ $mol_mem
		Source() {
			const obj = new this.$.$mol_page()

			obj.title = () => this.$.$mol_locale.text( '$hyoo_tree_Source_title' )
			obj.tools = () => [
				this.Lights(),
				this.Compile()
			] as readonly any[]
			obj.body = () => [
				this.Source_text()
			] as readonly any[]

			return obj
		}

		/**
		 * ```tree
		 * Close_icon $mol_icon_cross
		 * ```
		 */
		@ $mol_mem
		Close_icon() {
			const obj = new this.$.$mol_icon_cross()

			return obj
		}

		/**
		 * ```tree
		 * Close $mol_link
		 * 	sub / <= Close_icon
		 * 	arg * compile null
		 * ```
		 */
		@ $mol_mem
		Close() {
			const obj = new this.$.$mol_link()

			obj.sub = () => [
				this.Close_icon()
			] as readonly any[]
			obj.arg = () => ({
				compile: null as any
			})

			return obj
		}

		/**
		 * ```tree
		 * result \
		 * ```
		 */
		result() {
			return ""
		}

		/**
		 * ```tree
		 * Result_text $mol_text text <= result
		 * ```
		 */
		@ $mol_mem
		Result_text() {
			const obj = new this.$.$mol_text()

			obj.text = () => this.result()

			return obj
		}

		/**
		 * ```tree
		 * Result $mol_page
		 * 	title @ \Compiled
		 * 	tools / <= Close
		 * 	body / <= Result_text
		 * ```
		 */
		@ $mol_mem
		Result() {
			const obj = new this.$.$mol_page()

			obj.title = () => this.$.$mol_locale.text( '$hyoo_tree_Result_title' )
			obj.tools = () => [
				this.Close()
			] as readonly any[]
			obj.body = () => [
				this.Result_text()
			] as readonly any[]

			return obj
		}
	}

}
